'use strict';
/**
 * @api JUMP_PRODUCT({code,type}) JUMP_PRODUCT
 * @apiGroup APP
 * @apiVersion 1.0.0
 * @apiParam {productCode} code 产品编号
 * @apiParam {amount} type 产品类型-0:货基 1:短期理财型 2:弘康保险 3:P2C产品
 * @apiDescription APP 跳跳转至产品详情
 * @apiUse APPNpm
 * @apiUse APPJs
 * @apiParamExample {string} 例子
 * JUMP_PRODUCT('N7528',0)
 * @apiParamExample {string} 返回值
 * 无
 **/

/**
 * todo JUMP
 */
import {WinJSDec, objError} from './APP/config';

var name = 'JUMP_PRODUCT';
var JUMP_PRODUCT = (obj) => {
  if (typeof obj !== 'object') {
    throw new TypeError(`[${name}] ${objError}`);
  }
  var {code, type} = obj;
  WinJSDec(_ => {
    window.WinJSBridge.call('goods', 'showProduct', {code, type});
  });
};
export default JUMP_PRODUCT;
